/*  Poppins font imported from google fonts  */
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
  font-family: 'Poppins', sans-serif;
}
html{
  background-color: #e6fff2;
}

body{
  margin:20px 10% 20px 10%;
  padding:0;
  box-sizing: border-box;
  min-height: 60vh;
  border: 2px solid black;
  border-radius: 5px;
  background-color: white;
}

@media screen and (min-width:600px) {
  body{
    max-width: 600px;
    margin:auto;
    margin-top: 20px;
    margin-bottom:20px;
    background-color: white;
  }
}

.header{
  display: flex;
  justify-content: space-around;
  align-items: center;
  color:#ffff88; 
  font-size: 20px;
  overflow: hidden;
}

.button{
  padding: 5px 15px;
  background-color: green;
  border:none;
  color:white;
  border-radius: 3px;
  font-size: 20px;
  cursor: pointer;
}

.button-remove{
  background-color: red;
}

.task,.reminder{
  margin:0px 4px 10px 4px;
  padding:0px;
  background-color: #eee;
  min-height:80px;
  max-height: 500px;
  max-width:100%;
  overflow:auto;
}

.task h3{
  margin-top: 10px;
  padding-left:5px;
  margin-bottom: 1px;
}
.task p{
  margin-top:1px;
  padding-left:5px;
}

.cross{
  color:white;
  float:right;
  border:none;
  background-color: #ff0000;
  margin-right:25px;
  margin-top:20px;
  cursor: pointer;
  padding:8px 8px 2px;
  border-radius: 2px;
}

.reminder{
  border-left:5px solid green;
}

.form{
  margin:5px 20px 10px 20px;
}

.form input{
  width:100%;
  height: 25px;
}

.checkbox input{
  width:50%;
}

.form input:focus{
  outline:none !important;
  border:2px solid rgb(47, 213, 224);
  box-shadow: 0px 0px 5px rgb(10, 144, 233);
}

.checkbox input:focus{
  border:none;
  box-shadow: 0px 0px 0px;
}

.btn-submit input{
  background-color: black;
  color:white;
  font-family: 'Poppins';
  height:30px;
  border:none;
  margin-top:15px;
}
